.section-customer {
  //   padding-top: 185px;
  padding-bottom: 395px;
  @media screen and(min-width:768px) {
    padding: 0;
  }
}

.customer {
  text-align: center;

  background-image: url(../images/mobile/ice-cream-background-mobile/ice-cream-background-mobile.jpg);
  background-repeat: no-repeat;
  background-size: cover;

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(../images/mobile/ice-cream-background-mobile/ice-cream-background-mobile@2x.jpg);
  }

  @media screen and(min-width:768px) {
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../images/tablet/ice-cream-background-tablet/ice-cream-background-tablet@2x.jpg);
    }

    background-image: url(../images/tablet/ice-cream-background-tablet/ice-cream-background-tablet.jpg);

    padding-top: 220px;
    padding-bottom: 385px;
  }

  @media screen and(min-width:1200px) {
    background-image: url(../images/desktop/ice-cream-background/ice-cream-background.jpg);
    padding-top: 450px;
    padding-bottom: 195px;

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../images/desktop/ice-cream-background/ice-cream-background@2x.jpg);
    }
  }
}

.customer__img {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 32px;
}

.customer__desc-review {
  @include font-style(400, 12px, 31px);

  color: var(--secondary-text-color);
  background-color: var(--main-bg-color);
  padding: 34px 39px;
  border-radius: 10px;

  margin-bottom: 30px;

  @media screen and(min-width:768px) {
    @include font-style(400, 14px, 26px);
    letter-spacing: 0.04em;
    border-radius: 24px;
    padding: 29px 80px;
    margin-bottom: 32px;
  }

  @media screen and(min-width:1200px) {
    @include font-style(400, 16px, 23px);
    width: 968px;
    margin-left: auto;
    margin-right: auto;
    padding: 39px 74px;
  }
}

.customer__desk-name {
  @include font-style(500, 20px, 37px);

  margin-bottom: 14px;

  @media screen and(min-width:768px) {
    @include font-style(500, 16px, 30px);
    margin-bottom: 32px;
    font-size: 16px;
    line-height: 30px;
  }
}
.slick-list {
  overflow: hidden;
}

.slick-track {
  display: flex;
}

.slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -8px;
}

.slick-dots li {
  list-style: none;
  margin: 0 9px;
}

.slick-dots li.slick-active button {
  background-color: var(--main-bg-color);
}

.slick-dots li button {
  padding: 0;
  border: 0;
  font-size: 0;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #D9D9D9;
}
